import axios from 'axios/index'

import config from '../../config'
import action, { actionFailed } from '../../util/action'
import createAsyncConst from '../../util/createAsyncConst'

export const REMOVE_DISTRIBUTION_CENTER = createAsyncConst(
  'REMOVE_DISTRIBUTION_CENTER'
)
export const SUBMIT_DISTRIBUTION_CENTER = createAsyncConst(
  'SUBMIT_DISTRIBUTION_CENTER'
)
export const FETCH_DISTRIBUTION_CENTER = createAsyncConst(
  'FETCH_DISTRIBUTION_CENTER'
)
export const SAVE_DISTRIBUTION_CENTER_SUPPLIER = createAsyncConst(
  'SAVE_DISTRIBUTION_CENTER_SUPPLIER'
)
export const FETCH_DISTRIBUTION_CENTER_TO_SUPPLIER = createAsyncConst(
  'FETCH_DISTRIBUTION_CENTER_TO_SUPPLIER'
)
export const EDIT_DISTRIBUTION_CENTER = createAsyncConst(
  'EDIT_DISTRIBUTION_CENTER'
)
export const SAVE_DISTRIBUTION_CENTER_STORE = createAsyncConst(
  'SAVE_DISTRIBUTION_CENTER_STORE'
)
export const FETCH_DISTRIBUTION_CENTER_TO_STORE = createAsyncConst(
  'FETCH_DISTRIBUTION_CENTER_TO_STORE'
)

export function fetchDistributionCenters(page, filters = []) {
  return dispatch => {
    dispatch(action(FETCH_DISTRIBUTION_CENTER.ACTION))
    return axios
      .get(
        `${config.API_URL}/distributioncenter?filters=` +
          JSON.stringify(filters),
        {
          page,
          filters,
        }
      )
      .then(resp => {
        dispatch(action(FETCH_DISTRIBUTION_CENTER.SUCCESS, resp.data))
        return resp
      })
      .catch(e => dispatch(actionFailed(FETCH_DISTRIBUTION_CENTER, e)))
  }
}

export function edit(id) {
  return dispatch => {
    dispatch(action(EDIT_DISTRIBUTION_CENTER.ACTION, { id }))
    return axios
      .get(`${config.API_URL}/distributioncenter/${id}`)
      .then(resp => resp.data.data.item)
      .then(data => dispatch(action(EDIT_DISTRIBUTION_CENTER.SUCCESS, data)))
      .catch(e => dispatch(actionFailed(EDIT_DISTRIBUTION_CENTER, e)))
  }
}

export function post(values) {
  return submit(values, 'post')
}

export function update(values) {
  return submit(values, 'put')
}

function submit(values, method) {
  const id = values.id ? `/${values.id}` : ''

  return dispatch => {
    dispatch(action(SUBMIT_DISTRIBUTION_CENTER.ACTION))
    return axios[method](`${config.API_URL}/distributioncenter${id}`, values)
      .then(resp => resp.data)
      .then(data => {
        dispatch(action(SUBMIT_DISTRIBUTION_CENTER.SUCCESS, data))
        return data
      })
      .catch(e => dispatch(actionFailed(SUBMIT_DISTRIBUTION_CENTER, e)))
  }
}

export function remove(id) {
  return dispatch => {
    dispatch(action(REMOVE_USER.ACTION))
    return axios
      .delete(`${config.API_URL}/api/distributioncenter/${id}`)
      .then(resp => resp.data)
      .then(data => {
        dispatch(action(REMOVE_USER.SUCCESS, data))
        dispatch(fetchUsers())
      })
      .catch(e => actionFailed(REMOVE_USER.FAILURE, e))
  }
}

export function fetchDistributionCentersPerSuppliers(id) {
  return dispatch => {
    dispatch(action(FETCH_DISTRIBUTION_CENTER_TO_SUPPLIER.ACTION, { id }))
    return (
      axios
        .get(`${config.API_URL}/distributioncenterpersupplier/${id}`)
        .then(resp => resp.data.data.item)
        .then(data => {
          dispatch(action(FETCH_DISTRIBUTION_CENTER_TO_SUPPLIER.SUCCESS, data))
          return data
        })
        // .then(data => dispatch(action(FETCH_DISTRIBUTION_CENTER_TO_SUPPLIER.SUCCESS, data)))
        .catch(e =>
          dispatch(actionFailed(FETCH_DISTRIBUTION_CENTER_TO_SUPPLIER, e))
        )
    )
  }
}

// Associação entre Centro de Distribuição e Fornecedores
export function saveDCSupplier(values, id) {
  return dispatch => {
    dispatch(action(SAVE_DISTRIBUTION_CENTER_SUPPLIER.ACTION))
    return axios
      .post(`${config.API_URL}/supplier/${id}/distributioncenters`, values)
      .then(resp => resp.data)
      .then(data => {
        dispatch(action(SAVE_DISTRIBUTION_CENTER_SUPPLIER.SUCCESS, data))
        return data
      })
      .catch(e => dispatch(actionFailed(SAVE_DISTRIBUTION_CENTER_SUPPLIER, e)))
  }
}

// Associação entre Centro de Distribuição e Lojistas
export function fetchDistributionCentersPerStores(id) {
  return dispatch => {
    dispatch(action(FETCH_DISTRIBUTION_CENTER_TO_STORE.ACTION, { id }))
    return axios
      .get(`${config.API_URL}/store/${id}/distributioncenters`)
      .then(resp => resp.data.data.item)
      .then(data => {
        dispatch(action(FETCH_DISTRIBUTION_CENTER_TO_STORE.SUCCESS, data))
        return data
      })
      .catch(e => dispatch(actionFailed(FETCH_DISTRIBUTION_CENTER_TO_STORE, e)))
  }
}

export function saveDistributionCentersPerStores(values, id) {
  return dispatch => {
    dispatch(action(SAVE_DISTRIBUTION_CENTER_STORE.ACTION))
    return axios
      .post(`${config.API_URL}/store/${id}/distributioncenters/save`, values)
      .then(resp => resp.data)
      .then(data => {
        dispatch(action(SAVE_DISTRIBUTION_CENTER_STORE.SUCCESS, data))
        return data
      })
      .catch(e => dispatch(actionFailed(SAVE_DISTRIBUTION_CENTER_STORE, e)))
  }
}
